import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {descriptions} from "../../media/projects";
import { getCardClasses, getCardShape, getProjectsBackground, getTextTheme, setCardShape } from "../../store/Theme.store";
import ProjectList from "./ProjectList";

const Projects: React.FC = () => {
    const dispatch = useDispatch();
    const [selectedProject, setSelectedProject] = useState(-1);
    const backgroundClass = useSelector(getProjectsBackground);
    const textClass = useSelector(getTextTheme);
    const cardShape = useSelector(getCardShape);
    const cardClasses = useSelector(getCardClasses);
    const [cardColors, setCardColors] = useState(descriptions.map(() => cardClasses[Math.floor(Math.random() * cardClasses.length)]));

    useEffect(() => {
        setColors();
    }, [ cardClasses ]);

    const setColors = () => {
        const classes = descriptions.map(() => cardClasses[Math.floor(Math.random() * cardClasses.length)]);
        setCardColors(classes);
    }

    return (
        <div className={`full-page ${backgroundClass} ${textClass}`} id="projects">
            <div className="column-centered">
                <div className="row-center align-center border-bottom border-white px-3">
                    <p className="text-center text-header my-3">projects</p>
                    <h1 className="bi bi-arrow-repeat text-shadow-lg font-bold mx-3 pointer" onClick={() => setColors()} />
                </div>

                <div className="row-center mt-3">
                <i className={`fas fa-square fa-2x ${cardShape === 'card-bubble' ? 'text-white' : 'greyed-out'} mx-2 pointer`} onClick={() => dispatch(setCardShape('card-bubble'))}></i>
                    <i className={`fas fa-circle fa-2x ${cardShape === 'card-circle' ? 'text-white' : 'greyed-out'} mx-2 pointer`} onClick={() => dispatch(setCardShape('card-circle'))}></i>
                </div>
                {
                    selectedProject === -1 ?
                    <ProjectList 
                        projects={descriptions} 
                        cardClasses={cardColors}
                        selectProject={setSelectedProject} 
                    /> :
                    <div id="selected-project" className={`row border-20 my-4 mx-6 ${cardColors[selectedProject]}`}>
                        <div className="container p-3 lg-p-5">
                            <div className="row-between wrap align-center">
                                <div className="pointer" onClick={() => setSelectedProject(-1)}><i className="fas fa-times fa-2x" /></div>
                                <p className="text-center text-medium my-0" id="project-title">{descriptions[selectedProject].title || ""}</p>
                                <div className="hide"><i className="" /></div>
                            </div>
                            <p className="text-about line-height-150 my-3 mx-2" id="project-description" dangerouslySetInnerHTML={{__html: descriptions[selectedProject].description || ""}}></p>
                        </div>
                    </div>
                    /*
                        <div id="selected-project" className={`row border-20 my-4 mx-5 ${cardColors[selectedProject]}`}>
                        <div className="container p-3 lg-p-5 relative">
                            <div className="rel-top-left pointer" onClick={() => setSelectedProject(-1)}><i className="fas fa-times fa-2x" /></div>
                            <p className="text-center text-medium m-3" id="project-title">{descriptions[selectedProject].title || ""}</p>
                            <p className="text-about line-height-150 my-3 mx-2" id="project-description" dangerouslySetInnerHTML={{__html: descriptions[selectedProject].description || ""}}></p>
                        </div>
                    </div>
                    */
                }

                
            </div>
        </div>
    );
}

export default Projects;
